.HightBarContainer{
    display:flex;
    flex-direction: row;
    width: 100%;
    height: 60px;
    background-color: #292929;

    &_itemName{
        width: 50%;
        color:white;
        font-size: 35px;
        padding-left: 10px;

        img{
            padding-left: 10px;
        }
        }

    &_pagination{
        display: block !important;
        width: 50%;
        text-align: end;

        .backButton{
            width:60px;
            height: 30px;
            background: #464646 !important ;
            color:white ; 
            border:none;
            border-radius: 5px;
            margin: 15px 18px 0 0;

            &:hover{
                color:#2479D5;
            }
        }
        
    }
}